import React from 'react'
import './linkcommodity.css'
const Linkcommodity = (props) => {
    return (
        <div>

            <div class="navi-bar">
                <button class="btn-back">
                    <span class="arrow iconfont icon-fanhuitianchong" onClick={() => props.history.push('/')}></span>
                </button>
            </div>
            <div id="spuare">
                <img id="img-1" src={'https://gw.alicdn.com/bao/uploaded/i3/413996455/O1CN01IRGy451xYRBdhnCTz_!!413996455.jpg_500x500q90.jpg_.webp'}></img>
            </div>
            <div id="ad">
                <div>
                    <p id="money">￥ 33-34</p>
                </div>
                <div>
                    <p id="m">精品厨房收纳架</p>
                </div>
            </div>
            <div class="bottom-bar" >
                <div class="btn-1">
                    <span class="taobao-iconfont icon-highlight"><i class="f" className='iconfont icon-dianpu'></i></span>
                    <span class="btn-text">店铺</span>
                </div>
                <div class="btn-1">
                    <span class="taobao-iconfont"><i class="f" className='iconfont icon-kefu'></i></span>
                    <span class="btn-text">客服</span>
                </div>
                <div class="btn-1">
                    <span class="taobao-iconfont "><i class="f" className='iconfont icon-shoucang '></i></span>
                    <span class="btn-text">收藏</span>
                </div>
                <div class="bottom-bar-btn cart btn-pos-left ">
                    <span class="btn-title">加入购物车</span>
                </div>
                <div class="bottom-bar-btn buy btn-pos-right ">
                    <span class="btn-title">立即购买</span>
                </div>
            </div>
        </div>
    )
}

export default Linkcommodity;
